
import Search from 'vux/src/components/search/index.vue'

import subList from './subList'
import { searchMajor } from '@/api/index'
export default {
components: { Search, subList },
data () {
return {
loading: false,
key: '',
placeholder: '搜索',
list: [],
query: {
size: 0,
name: ''
},
end: false
}
},
computed: {
toUrl () {
return this.$store.state.user.returnUrl
},
majorCode () {
return this.$route.params.majorname || ''
}
},
mounted () {
this.$nextTick(() => {
this.$refs.search.isFixed = true
this.$refs.search.isFocus = true
this.$refs.search.isCancel = false
this.$refs.search.setFocus()
this.$refs.list.init()
})
},
methods: {
returnBack () {
this.list = []
this.key = ''
this.$router.go(-1)
},

nextPage () {
this.page++
this.getList()
},

reList () {
this.page = 0
this.list = []
this.end = false
this.loading = true
this.getList()
this.$nextTick(() => {
this.$refs.list.init()
})
},

searchKey (key) {
this.page = 0
this.query = {
name: key,
page: this.page
}
this.list = []
this.end = false
if (key !== undefined && key !== '') {
this.loading = true
this.getList()
}
},
getList () {
this.query.page = this.page
searchMajor(this.query).then(res => {
this.loading = false
if (res.data.code === 0) {
this.list = this.list.concat(res.data.data)
if (res.data.data.length < 20) {
this.end = true
}
this.$nextTick(() => {
this.$refs.list.majorScroll && this.$refs.list.majorScroll.refresh()
})
} else {
this.end = true
}
})
},

getCourses (item) {
this.$emit('getCourses', item)
}
}
}



