import _ from 'underscore'
import BScroll from 'better-scroll'
export default {
  data () {
    return {
      majorScroll: null
    }
  },
  props: {
    loading: Boolean,
    list: Array,
    type: String,
    end: Boolean
  },
  mounted () {
    this.init()
  },
  watch: {
    $route () {
      this.init()
    }
  },
  methods: {
    init () {
      this.$nextTick(() => {
        this.majorScroll = new BScroll(this.$refs.majorWrapper, {
          click: true,
          pullDownRefresh: {
            threshold: 50,
            stop: 40
          },
          pullUpLoad: {
            threshold: 0,
            moreTxt: '加载中',
            noMoreTxt: '没有更多数据了'
          }
        })
        this.majorScroll.on('pullingUp', (pos) => {
          console.log('pullingUp')
          if (!this.end && !this.loading) {
            this.nextPage()
            this.$nextTick(() => {
              this.majorScroll.finishPullUp()
            })
          }
        })
        this.majorScroll.on('pullingDown', () => {
          this.reList()
          this.$nextTick(() => {
            this.majorScroll.finishPullDown()
          })
        })
      })
    },
    nextPage: _.debounce(function () {
      this.$emit('nextPage')
    }, 50, true),
    reList: _.debounce(function () {
      this.$emit('reList')
    }, 50, true),
    // 去专业详情
    gotoPage (sub) {
      this.$router.push({ path: '/majorInfo/' + sub.majorcode })
    }
  }
}



